<template>
  <v-navigation-drawer
    v-model="inputValue"
    app dark clipped
    v-if="profile && profile.role"
    class="grey darken-4"
  >
    <div class="w-full h-full">
      <div class="w-full h-full">
      <v-divider></v-divider>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="title text-center">
            {{ profile.full_name }}
          </v-list-item-title>
          <v-list-item-subtitle
            class="text-center text-button"
            :class="roleColor(profile.role.name, true)"
          >
            {{ profile.role.name }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <template v-if="profile.role.name === 'User'">
        <v-divider></v-divider>
        <v-list-item>
          <v-list-item-content>
            <v-select
              v-model="selectedAccountModel"
              class="remove-error-message-slot"
              prepend-inner-icon="fas fa-user"
              item-text="name"
              :items="accounts"
              label="Account" return-object
              dense outlined
            ></v-select>
          </v-list-item-content>
        </v-list-item>
      </template>
      <v-divider></v-divider>
      <v-list dense nav rounded dark class="mt-5">
        <v-list-item
          v-for="item in items"
          :key="item.title"
          :to="item.path"
          active-class="custom-yellow"
        >
          <v-list-item-icon>
            <v-icon small>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </div>
  </div>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters } from 'vuex'
import adminNavigation from '@/json/admin-navigation.json'
import sellerNavigation from '@/json/seller-navigation.json'
import userNavigation from '@/json/user-navigation.json'

export default {
  props: {
    value: {
      type: Boolean,
      required: false
    }
  },
  data: () => ({
    adminItems: adminNavigation,
    sellerItems: sellerNavigation,
    userItems: userNavigation
  }),
  computed: {
    ...mapGetters({
      profile: 'profile',
      role: 'role',
      accounts: 'accounts',
      selectedAccount: 'selectedAccount'
    }),
    items () {
      switch (this.role) {
        case 'Admin':
          return this.adminItems
        case 'Seller':
          return this.sellerItems
        default:
          return this.userItems
      }
    },
    inputValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
      }
    },
    selectedAccountModel: {
      get () {
        return this.selectedAccount
      },
      set (val) {
        this.$store.commit('setSelectedAccount', val)
      } 
    }
  }
}
</script>

<style scoped>
a {
    font-size: 15px;
    color: #757575  !important;
    text-decoration: none;
}
i {
  width: 10px;
  margin-left: 15px;
}
</style>
